import React, { useState, useEffect, Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

const Images = ({ match }) => {
  const [imageState, updateImageState] = useState([]);

  // Retrieves the list of items from the Express app
  const getImages = () => {
    fetch("/api/getBaseImages")
      // .then((img) => img.json())
      .then((res) => {
        console.log("Received images is:", res);
        updateImageState(res);
      });
  };

  useEffect(() => {
    getImages();
  }, []);

  return (
    <div className="App">
      <h1>List of Items</h1>
      <h2>Plop</h2>
      {/* Check to see if any items are found*/}
      <img alt="scan" src={require("../../images/ppp.png")}></img>
      {imageState.length ? (
        <div>
          {imageState.map((imgUrl) => (
            <img alt="scan" src={imgUrl}></img>
          ))}
        </div>
      ) : (
        <div>Loading...</div>
      )}
      {/* <Route path={`${match.url}/:articleId`} component={Img} /> */}
    </div>
  );
};

const Img = ({ match }) => {
  const [imageState, updateImageState] = useState([]);

  // Retrieves the list of items from the Express app
  let flag = false;
  const getImages = () => {
    fetch("/api/getImages")
      .then((img) => img.json())
      .then((res) => {
        console.log(res);
        updateImageState(res.files);
        if (!flag) {
          flag = true;
        }
      });
  };

  useEffect(() => {
    getImages();
  }, [imageState, flag]);

  function DisplayImages(imgList = imageState) {
    if (imgList.length === 0) {
      return <div>No Images</div>;
    } else {
      return (
        <ul>
          {imgList.map((imgUrl) => {
            return (
              <li>
                <img source={imgUrl}></img>
              </li>
            );
          })}
        </ul>
      );
    }
  }

  getImages();
};

export default Images;
